}
.content.with-sidebar {
    margin-left: 290px;
}
.content {
  position: relative;
  padding: 35px 0;
  /* max-width: 700px; */
  margin: 0 auto;
  padding-left: 50px;
  /* border: 2px; */
  /* border-style:solid; */
  /* border-color:#000000; */
}
/* sidebar */
.sidebar {
    position: fixed;
    z-index: 10;
    top: 61px;
    left: 0;
    bottom: 0;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: none;
}
.sidebar li {
    margin-top: 0.5em;
}
.sidebar ul {
    list-style-type: none;
    margin: 0;
    line-height: 1.5em;
    padding-left: 1em;
}
.sidebar .sidebar-link {
    color: #4f5959;
}
.sidebar .sidebar-link.current {
    font-weight: 600;
    color: #42b983;
}
#main.fix-sidebar {
  position: static;
}
/* sidebar */
#main {
    position: relative;
    z-index: 1;
    padding: 0 60px 30px;
    overflow-x: hidden;
}
body #header {
  position: fixed;
  width: 100%;
  top: 0;
}
#header {
  background-color: #fff;
  height: 40px;
  /* padding: 10px 60px; */
  padding: 10px;
  position: relative;
  z-index: 20;
  -webkit-box-shadow: 0 0 1px rgba(0,0,0,0.25);
          box-shadow: 0 0 1px rgba(0,0,0,0.25);
  -webkit-transition: background-color 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out;
}
#nav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    position: absolute;
    right: 30px;
    top: 10px;
    height: 40px;
    line-height: 40px;
}
body.docs #nav {
  position: fixed;
}
#nav .nav-link {
  cursor: pointer;
}
.nav-link.current {
  border-bottom: 3px solid #42b983;
}
.nav-link {
  padding-bottom: 3px;
  white-space: nowrap;
}
#nav .nav-dropdown-container .arrow {
  pointer-events: none;
}
#nav .arrow {
  display: inline-block;
  vertical-align: middle;
  margin-top: -1px;
  margin-left: 6px;
  margin-right: -14px;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 5px solid #4f5959;
}
#nav .nav-dropdown {
  display: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  max-height: calc(100vh - 61px);
  overflow-y: auto;
  position: absolute;
  top: 100%;
  right: -15px;
  background-color: #fff;
  padding: 10px 0;
  border: 1px solid #ddd;
  border-bottom-color: #ccc;
  text-align: left;
  border-radius: 4px;
  white-space: nowrap;
}
#nav .nav-dropdown li {
  line-height: 1.8em;
  margin: 0;
  display: block;
}
#nav li {
  display: inline-block;
  position: relative;
  margin: 0 0.6em;
}
#nav .nav-dropdown li:first-child h4 {
  margin-top: 0;
  padding-top: 0;
  border-top: 0;
}
#nav .nav-dropdown h4 {
  margin: 0.45em 0 0;
  padding-top: 0.45em;
  border-top: 1px solid #eee;
}
#nav .nav-dropdown a, #nav .nav-dropdown h4 {
  padding: 0 24px 0 20px;
}
h1, h2, h3, h4, strong {
  font-weight: 600;
  color: #273849;
}
h3 {
    display: block;
    font-size: 1.17em;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    font-weight: bold;
}
#logo {
  display: inline-block;
  font-size: 1.5em;
  line-height: 40px;
  color: #273849;
  font-family: "Dosis", "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
  font-weight: 500;
}
a {
  text-decoration: none;
  color: #304455;
}
#logo img {
  vertical-align: middle;
  margin-right: 6px;
  width: 40px;
  height: 40px;
}
.search-query {
  height: 30px;
  line-height: 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 15px 0 30px;
  border: 1px solid #e3e3e3;
  color: #273849;
  outline: none;
  border-radius: 15px;
  margin-right: 10px;
  -webkit-transition: border-color 0.2s ease;
  transition: border-color 0.2s ease;
  background: #fff url(data:image/png;base64,) 8px 5px no-repeat;
  background-size: 20px;
  vertical-align: middle !important;
}
img {
  border: none;
}

